.popup {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, .8);
  min-height: 100%;
  align-items: center;
  justify-content: center;
  display: flex;
  visibility:hidden;
  opacity:0;
  transition:visibility .3s linear,opacity .3s linear;
}